import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-help-rpg',
  templateUrl: './help-rpg.component.html',
  styleUrls: ['./help-rpg.component.scss'],
})
export class HelpRpgComponent implements OnInit {

  @Output('prosseguir')
  prosseguir: EventEmitter<any> = new EventEmitter();
  proximo(){
    this.prosseguir.emit('PRÓXIMO!');
  }

  @Input()
  hasNext: boolean = true;

  abaAtual: string = 'parte1';

  constructor() { }

  ngOnInit() {}

}
